.alert-bar {
    position: relative;
    background: var(--wgo20a);




    /* Alert Link Only */
    .alert-link {
        top: 0;
        text-align: center;
        box-shadow: rgba(0, 0, 0, .6) 0 .5rem 8px;
        box-shadow: 0;
        a {
            display: block;
            background: #fff;
            padding: 1rem 10%;
            color: #555;
            font-weight: 600;
            font-size: 1rem;
            text-decoration: none;
            border-bottom: #e6b711 solid 2px;


            &:hover,
            &:focus {
                background: #f9eecc;
                /*border-bottom: #66582d solid 2px;*/
            }
            &.alert-label {
                font-size: 1.3rem;
                color: #00573d;
                font-weight: 700;
                position: relative;
                &:hover,
                &:focus {
                    color: #333;
                    text-decoration: underline;
                }
            }

            .alert-date {
                @media screen and (max-width: 600px) {
                    display: block;
                }
                @media screen and (min-width:600px) {
                    display: inline;
                }
                &:hover {
                    text-decoration: none;
                }
            }
        }
    }

    /* 2 Alert Links ( used with Flex: div > a + a ) ) */
    /* shares some styles from .alert-link above */
    .alert-flex {
        //
        //
        // Open As Flex Area. Extend with new override file
        //
        //
        h2 {
            text-align: center;
        }
        .two-buttons {
            @media screen and (min-width: 560px) {
                display: flex;
                justify-content: center;
                padding: 1rem 10%;
            }
            @media screen and (min-width:720px) {
                padding: 1rem 5%;
            }

            @media screen and (min-width:1200px) {
                padding: 1rem 0;
            }
            a {
                text-align: center;
                @media screen and (min-width: 560px) {
                    justify-content: center;
                    align-items: center;
                    margin-right: 1rem;
                    background: #fff;
                    border: #e6b711 solid 2px;
                    border-radius: 8px;
                    &:nth-child(even) {
                        margin-right: 0;
                    }
                }
                @media screen and (min-width:720px) {
                    flex: 1 1 50%;
                }
            }
        }
    }
}

@import "override-alertbar.css";
